/* Only demo */
@media (max-width: 1000px) {
    .welcome-message {
        display: none;
    }
}

input{
    filter: none;
}


.modal-xl-100vw{
    max-width: 100vw;
}

.modal-xl-90vw{
    max-width: 90vw;
}

.modal-xl-80vw{
    max-width: 80vw;
}

.modal-xl-70vw{
    max-width: 70vw;
}

.modal-xl-60vw{
    max-width: 60vw;
}

.w-60{
    width: 60%;
}

.w-65{
    width: 65%;
}

.w-70{
    width: 70%;
}


@for $i from 1 through 5 {
    .letter-space-#{$i}{
        letter-spacing: $i * 0.1rem;
    }
}




.border-radius-xl{
    border-radius: $radiusXl;
}

.border-radius-right-xl{
    border-top-right-radius: $radiusXl;
    border-bottom-right-radius: $radiusXl;
}

.border-radius-left-xl{
    border-top-left-radius: $radiusXl;
    border-bottom-left-radius: $radiusXl;
}


.loader {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    z-index: 9999999 !important;
}

.loader-backdrop {
    position:fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.65);
    z-index:999999;
}





