.nav > li > a {
    color: $nav-text-color;
    font-weight: 600;
    padding: 14px 20px 14px 25px;
    display: block;
}

.nav.metismenu > li {
    display: block;
    width: 100%;
    position: relative;
}

.nav.metismenu .dropdown-menu > li > a {
    padding: 3px 20px;
    display: block;
}

.nav.navbar-right > li > a {
    color: #999c9e;
}

.nav > li.active > a {
    color: #ffffff;
}

.navbar-default .nav > li > a:hover, .navbar-default .nav > li > a:focus {
    background-color: darken($nav-bg, 3%);
    color: white;
}

.nav .open > a, .nav .open > a:hover, .nav .open > a:focus {
    background: #fff;
}

.nav.navbar-top-links > li > a:hover, .nav.navbar-top-links > li > a:focus {
    background-color: transparent;
}

.navbar-static-side .nav > li > a > i {
    margin-right: 0.5rem;
    font-size: 1.2rem;
    position: relative;
    top: 0.1rem;
}

.navbar-static-side .nav > li > a {
    color: $nav-text-color;
    font-weight: 500;
    padding: 0.25rem 1.2rem 0.25rem 1.2rem;
    display: block;
    font-size: 0.9rem;
    transition: all ease-in-out 0.6s;
}

.navbar {
    border: 0;
}


.navbar-brand {
    display: inline-block;
    padding: 0;
    margin: 0;
    font-size: 1rem;
    line-height: inherit;
    white-space: nowrap;
    position: relative;
    top: 1rem;
}

.navbar-default {
    background-color: transparent;
    border-color: $nav-bg;
}

.body-small .navbar-top-links li:last-child {
    margin-right: 0;
}

.dropdown-menu {
    border: medium none;
    border-radius: $border-radius;
    box-shadow: 0 0 1rem -0.25rem rgba(86, 96, 117, 0.15);
    float: left;
    font-size: 0.9rem;
    left: 0;
    list-style: none outside none;
    padding: 0;
    position: absolute;
    text-shadow: none;
    top: 100%;
    z-index: 1000;
}

.dropdown-menu > li > a {
    border-radius: 3px;
    color: inherit;
    line-height: 25px;
    margin: 4px;
    text-align: left;
    font-weight: normal;
    display: block;
    padding: 3px 20px;
}

.dropdown-menu > li > a:focus, .dropdown-menu > li > a:hover {
    color: #262626;
    text-decoration: none;
    background-color: #f5f5f5;
}

.dropdown-menu > .active > a,
.dropdown-menu > .active > a:focus,
.dropdown-menu > .active > a:hover {
    color: #fff;
    text-decoration: none;
    background-color: $navy;
    outline: 0;
}

.dropdown-menu > li > a.font-bold {
    font-weight: 600;
}

.navbar-top-links .dropdown-menu li {
    display: block;
}

.navbar-top-links .dropdown-menu li:last-child {
    margin-right: 0;
}

.navbar-top-links .dropdown-menu li a {
    padding: 0.25rem 1.2rem;
    margin: 0.5rem;
    font-size: 1rem;
    min-height: 0;
    -webkit-transition: $trasition ;
    -moz-transition: $trasition ;
    -ms-transition: $trasition ;
    -o-transition: $trasition ;
    transition: $trasition ;
}


.navbar-top-links .dropdown-menu > li > a:focus,
.navbar-top-links .dropdown-menu > li > a:hover {
    color: $dark;
    text-decoration: none;
    background-color: $gray;
}

.navbar-top-links .dropdown-menu li a div {
    white-space: normal;
}

.navbar-top-links .dropdown-messages,
.navbar-top-links .dropdown-tasks,
.navbar-top-links .dropdown-alerts {
    width: 310px;
    min-width: 0;
}

.navbar-top-links .dropdown-messages {
    margin-left: 5px;
}

.navbar-top-links .dropdown-tasks {
    margin-left: -59px;
}

.navbar-top-links .dropdown-alerts {
    margin-left: -123px;
}

.navbar-top-links .dropdown-user {
