        text-align: right;
    }

    .center-orientation .cssanimations .vertical-timeline-content.is-hidden {
        visibility: hidden;
    }
}

/* Tabs */

.tabs-container {

    .panel-body {
        background: #fff;
        border: 1px solid $border-color;
        border-radius: 2px;
        padding: 20px;
        position: relative;
    }

    .nav-tabs > li.active > a,
    .nav-tabs > li.active > a:hover,
    .nav-tabs > li.active > a:focus {
        border: 1px solid $border-color;
        border-bottom-color: transparent;
        background-color: #fff;
    }

    .nav-tabs > li {
        float: left;
        margin-bottom: -1px;
    }

    .tab-pane .panel-body {
        border-top: none;
    }

    .nav-tabs > li.active > a, .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
        border: 1px solid $border-color;
        border-bottom-color: transparent;
    }

    .nav-tabs {
        border-bottom: 1px solid $border-color;
    }

    .tab-pane .panel-body {
        border-top: none;
    }

    .tabs-left .tab-pane .panel-body, .tabs-right .tab-pane .panel-body {
        border-top: 1px solid $border-color;
    }

    .tabs-below > .nav-tabs,
    .tabs-right > .nav-tabs,
    .tabs-left > .nav-tabs {
        border-bottom: 0;
    }

    .tabs-left .panel-body {
        position: static;
    }

    .tabs-left > .nav-tabs, .tabs-right > .nav-tabs {
        width: 20%;
    }

    .tabs-left .panel-body {
        width: 80%;
        margin-left: 20%;
    }

    .tabs-right .panel-body {
        width: 80%;
        margin-right: 20%;
    }

    .tab-content > .tab-pane,
    .pill-content > .pill-pane {
        display: none;
    }

    .tab-content > .active,
    .pill-content > .active {
        display: block;
    }

    .tabs-below > .nav-tabs {
        border-top: 1px solid $border-color;
    }

    .tabs-below > .nav-tabs > li {
        margin-top: -1px;
        margin-bottom: 0;
    }

    .tabs-below > .nav-tabs > li > a {
        -webkit-border-radius: 0 0 4px 4px;
        -moz-border-radius: 0 0 4px 4px;
        border-radius: 0 0 4px 4px;
    }

    .tabs-below > .nav-tabs > li > a:hover,
    .tabs-below > .nav-tabs > li > a:focus {
        border-top-color: $border-color;
        border-bottom-color: transparent;
    }

    .tabs-left > .nav-tabs > li,
    .tabs-right > .nav-tabs > li {
        float: none;
        word-break: break-word;
        width: 100%;
    }

    .tabs-left > .nav-tabs > li > a,
    .tabs-right > .nav-tabs > li > a {
        min-width: 124px;
        margin-right: 0;
        margin-bottom: 3px;
    }

    .tabs-left > .nav-tabs {
        float: left;
        margin-right: 19px;
    }

    .tabs-left > .nav-tabs > li > a {
        margin-right: -1px;
        -webkit-border-radius: 4px 0 0 4px;
        -moz-border-radius: 4px 0 0 4px;
        border-radius: 4px 0 0 4px;
    }

    .tabs-left > .nav-tabs a.active,
    .tabs-left > .nav-tabs a.active:hover,
    .tabs-left > .nav-tabs a.active:focus {
        border-color: $border-color transparent $border-color $border-color;
    }

    .tabs-right > .nav-tabs {
        float: right;
        margin-left: 19px;
    }

    .tabs-right > .nav-tabs > li > a {
        margin-left: -1px;
        -webkit-border-radius: 0 4px 4px 0;
        -moz-border-radius: 0 4px 4px 0;
        border-radius: 0 4px 4px 0;
    }

    .tabs-right > .nav-tabs a.active,
    .tabs-right > .nav-tabs a.active:hover,
    .tabs-right > .nav-tabs a.active:focus {
        border-color: $border-color $border-color $border-color transparent;
        z-index: 1;
    }

    .tabs-right > .nav-tabs li {
        z-index: 1;
    }

}

.nav-tabs .nav-link:not(.active):focus, .nav-tabs .nav-link:not(.active):hover {
    border-color: transparent;
}

@media (max-width: 767px) {
    .tabs-container .nav-tabs > li {
        float: none !important;
    }

    .tabs-container .nav-tabs > li.active > a {
        border-bottom: 1px solid #e7eaec !important;
        margin: 0;
    }
}

/* jsvectormap */
.jvectormap-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.jvectormap-tip {
    position: absolute;
    display: none;
    border: solid 1px #CDCDCD;
    border-radius: 3px;
    background: #292929;
    color: white;
    font-family: sans-serif, Verdana;
    font-size: smaller;
    padding: 5px;
}

.jvectormap-zoomin, .jvectormap-zoomout, .jvectormap-goback {
    position: absolute;
    left: 10px;
    border-radius: 3px;
    background: $navy;
    padding: 3px;
    color: white;
    cursor: pointer;
    line-height: 10px;
    text-align: center;
    box-sizing: content-box;
}

.jvectormap-zoomin, .jvectormap-zoomout {
    width: 10px;
    height: 10px;
}

.jvectormap-zoomin {
    top: 10px;
}

.jvectormap-zoomout {
    top: 30px;
}

.jvectormap-goback {
    bottom: 10px;
    z-index: 1000;
    padding: 6px;
}

.jvectormap-spinner {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: center no-repeat url(data:image/gif;base64,);
}

.jvectormap-legend-title {
    font-weight: bold;
    font-size: 14px;
    text-align: center;
}

.jvectormap-legend-cnt {
    position: absolute;
}

.jvectormap-legend-cnt-h {
    bottom: 0;
    right: 0;
}

.jvectormap-legend-cnt-v {
    top: 0;
    right: 0;
}

.jvectormap-legend {
    background: black;
    color: white;
    border-radius: 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend {
    float: left;
    margin: 0 10px 10px 0;
    padding: 3px 3px 1px 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend .jvectormap-legend-tick {
    float: left;
}

.jvectormap-legend-cnt-v .jvectormap-legend {
    margin: 10px 10px 0 0;
    padding: 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick {
    width: 40px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-sample {
    height: 15px;
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle;
}

.jvectormap-legend-tick-text {
    font-size: 12px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
    text-align: center;
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
    display: inline-block;
    vertical-align: middle;
    line-height: 20px;
    padding-left: 3px;
}

/*Slick Carousel */

.slick-prev:before,
.slick-next:before {
    color: $navy !important;
}

/* Payments */

.payment-card {
    background: #ffffff;
    padding: 20px;
    margin-bottom: 25px;
    border: 1px solid $border-color;
}

.payment-icon-big {
    font-size: 60px;
    color: $light-gray;
}

.payments-method.panel-group .panel + .panel {
    margin-top: -1px;
}

.payments-method .panel-heading {
    padding: 15px;
    background-color: $gray;
}

.payments-method .panel-default {
    border: 1px solid $border-color;
}

.payments-method .panel {
    border-radius: 0;
}

.payments-method .panel-heading h5 {
    margin-bottom: 5px;
}

.payments-method .panel-heading i {
    font-size: 26px;
}

/* Select2 custom styles */

.select2-container--default .select2-selection--single,
.select2-container--default .select2-selection--multiple {
    border-color: $border-color;
}

.select2-container--default.select2-container--focus .select2-selection--single,
.select2-container--default.select2-container--focus .select2-selection--multiple {
    border-color: $navy;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
    background-color: $navy;
}

.select2-container--default .select2-search--dropdown .select2-search__field {
    border-color: $border-color;
}

.select2-dropdown {
    border-color: $border-color;

    input:focus {
        outline: none;
    }
}

.select2-selection {
    outline: none;
}

.ui-select-container.ui-select-bootstrap .ui-select-choices-row.active > a {
    background-color: $navy;
}

/* Tour */

.tour-tour .btn.btn-default {
    background-color: #ffffff;
    border: 1px solid #d2d2d2;
    color: inherit;
}

.tour-step-backdrop {
    z-index: 2101;
}

.tour-backdrop {
    z-index: 2100;
    opacity: .7;
}

.popover[class*=tour-] {
    z-index: 2100;
}

.popover-header {
    margin-top: 0;
}

body.tour-open .animated {
    animation-fill-mode: initial;
}

.tour-tour .btn.btn-secondary {
    background-color: #ffffff;
    border: 1px solid #d2d2d2;
    color: inherit;
}

/* Resizable */

.resizable-panels .ibox {
    clear: none;
    margin: 10px;
    float: left;
    overflow: hidden;
    min-height: 150px;
    min-width: 150px;
}

.resizable-panels .ibox .ibox-content {
    height: calc(100% - 49px);
}

.ui-resizable-helper {
    background: rgba(211, 211, 211, 0.4)

}

/* Wizard step fix */
.wizard > .content > .body {
    position: relative
}

/* PDF js style */

.pdf-toolbar {
    max-width: 600px;
    margin: 0 auto;
}

/* Dropzone */

.dropzone {
    min-height: 140px;
    border: 1px dashed $navy;
    background: white;
    padding: 20px 20px;

    .dz-message {
        font-size: 16px;

    }
}

/* Activity stream */

.stream {

    position: relative;
    padding: 10px 0;

    &:first-child .stream-badge:before {
        top: 10px;
    }

    &:last-child .stream-badge:before {
        height: 30px;
    }

    .stream-badge {
        width: 50px;

        i {
            border: 1px solid $border-color;
            border-radius: 50%;
            padding: 6px;
            color: lighten($text-color, 10%);
            position: absolute;
            background-color: #ffffff;
            left: 8px;

            &.fa-circle {
                color: lighten($text-color, 40%);
            }

            &.bg-success {
                color: #ffffff;
                background-color: $blue;
                border-color: $blue;
            }

            &.bg-primary {
                color: #ffffff;
                background-color: $navy;
                border-color: $navy;
            }

            &.bg-warning {
                color: #ffffff;
                background-color: $yellow;
                border-color: $yellow;
            }

            &.bg-info {
                color: #ffffff;
                background-color: $lazur;
                border-color: $lazur;
            }

            &.bg-danger {
                color: #ffffff;
                background-color: $red;
                border-color: $red;
            }

        }

        &:before {
            content: '';
            width: 1px;
            background-color: $border-color;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 20px;

        }

    }

    .stream-info {
        font-size: 12px;
        margin-bottom: 5px;

        img {
            border-radius: 50%;
            width: 18px;
            height: 18px;
            margin-right: 2px;
            margin-top: -4px;
        }

        .date {
            color: lighten($text-color, 20%);
            font-size: 80%;
        }

    }

    .stream-panel {
        margin-left: 55px;
    }

}

.stream-small {
    margin: 10px 0;

    .label {
        padding: 2px 6px;
        margin-right: 2px;
    }
}

/* Code */

pre {
    display: block;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    line-height: 1.42857143;
    color: #333;
    word-break: break-all;
    word-wrap: break-word;
    background-color: lighten($light-gray, 10%);
    border: 1px solid $light-gray;
    border-radius: 2px;
}

code, kbd, pre, samp {
    font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}
