    margin-left: 62px;
}

.social-feed-separated .social-avatar {
    float: left;
    padding: 0;
}

.social-feed-separated .social-avatar img {
    width: 52px;
    height: 52px;
    border: 1px solid #e7eaec;
}

.social-feed-separated .social-feed-box .social-avatar {
    padding: 15px 15px 0 15px;
    float: none;
}

.social-feed-box {
    /*padding: 15px;*/
    border: 1px solid #e7eaec;
    background: #fff;
    margin-bottom: 15px;
}

.article .social-feed-box {
    margin-bottom: 0;
    border-bottom: none;
}

.article .social-feed-box:last-child {
    margin-bottom: 0;
    border-bottom: 1px solid $border-color;
}

.article .social-feed-box p {
    font-size: 13px;
    line-height: 18px;
}

.social-action {
    margin: 15px;
}

.social-avatar {
    padding: 15px 15px 0 15px;
}

.social-comment .social-comment {
    margin-left: 45px;
}

.social-avatar img {
    height: 40px;
    width: 40px;
    margin-right: 10px;
}

.social-avatar .media-body a {
    font-size: 14px;
    display: block;
}

.social-body {
    padding: 15px;
}

.social-body img {
    margin-bottom: 10px;
}

.social-footer {
    border-top: 1px solid #e7eaec;
    padding: 10px 15px;
    background: #f9f9f9;
}

.social-footer .social-comment img {
    width: 32px;
    margin-right: 10px;
}

.social-comment:first-child {
    margin-top: 0;
}

.social-comment {
    margin-top: 15px;
}

.social-comment textarea {
    font-size: 12px;
}

/* Vote list */

.vote-item {
    padding: 20px 25px;
    background: #ffffff;
    border-top: 1px solid $border-color;
}

.vote-item:last-child {
    border-bottom: 1px solid $border-color;
}

.vote-item:hover {
    background: lighten($gray, 3%);
}

.vote-actions {
    float: left;
    width: 30px;
    margin-right: 15px;
    text-align: center;

}

.vote-actions a {
    color: $navy;
    font-weight: 600;
}

.vote-actions {
    font-weight: 600;
}

.vote-title {
    display: block;
    color: inherit;
    font-size: 18px;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 2px;
}

.vote-title:hover, .vote-title:focus {
    color: inherit;
}

.vote-info, .vote-title {
    margin-left: 45px;
}

.vote-info, .vote-info a {
    color: lighten($text-color, 30%);
    font-size: 12px;
}

.vote-info a {
    margin-right: 10px;
}

.vote-info a:hover {
    color: $navy;
}

.vote-icon {
    text-align: right;
    font-size: 38px;
    display: block;
    color: lighten($text-color, 50%);
}

.vote-icon.active {
    color: $navy;
}

body.body-small .vote-icon {
    display: none;
}

.lightBoxGallery {
    text-align: center
}

.lightBoxGallery img {
    margin: 5px
}

