}

.link-block {
    font-size: 12px;
    padding: 10px;
}

.nav.navbar-top-links .link-block a {
    font-size: 12px;
}

.navbar-top-links {
    text-align: right
}

.link-block a {
    font-size: 10px;
    color: inherit;

}

